class deviceDetectorService {
    constructor($window) {
        this.$window = $window;
    }

    isTouchedDevice() {
        return 'ontouchstart' in this.$window;
    }
}

angular.module('device-detection', [])
    .service('deviceDetectorService', deviceDetectorService);
angular.module('ps.filters', [])
    .filter('htmlToPlaintext', function () {
        return function(text) {
            return String(text || "").replace(/<[^>]+>/gm, '');
        };
    }
);
/**
 * Created by kevin on 14/08/2014.
 */

_.mixin({
    // Update in place, does not preserve order
    updateinplace : function(localArray, remoteArray, comparisonFunction = (inArray, elem) => inArray.indexOf(elem), withOrder = false) {

        // Remove from localArray what is not in the remote array :
        _.forEachRight(localArray.slice(), (elem, key) => (comparisonFunction(remoteArray, elem) === -1) && localArray.splice(key, 1));

        // Add to localArray what is new in the remote array :
        _.forEach(remoteArray, (elem) => (comparisonFunction(localArray, elem) === -1) && localArray.push(elem));

        if (withOrder) {
            _.forEach(remoteArray, (elem, key) => {
                var elementToMove = localArray.splice(comparisonFunction(localArray, elem), 1)[0];
                localArray.splice(key, 0, elementToMove);
            });
        }
        
        return localArray;
    }
});

class navbarController {
    constructor() {
        this.navCollapsed = true;
    }
}

class navbarDirective {
    constructor() {
        this.transclude = true;
        this.replace = true;
        this.restrict = 'E';
        this.scope = true;
        this.templateUrl = 'html/navbar.html';
        this.controller = 'navbarController';
        this.controllerAs = 'navbar';
    }

    link(scope, element) {
        element.removeClass('hidden');
    }
}

angular.module('navbar', [])
    .directive('navbar', () => new navbarDirective())
    .controller('navbarController', navbarController);

class UpdatingDirective {
    constructor() {
        this.restrict = 'E';
        this.scope = true;
        this.templateUrl = 'html/updating.html';
        this.controller = 'UpdatingController';
        this.controllerAs = 'uc';
    }

    link(scope, element) {

        let liParent = element.parent().parent()[0];

        if (liParent && liParent.tagName === 'LI') {
            let liElement = angular.element(liParent);
            liElement.addClass('hidden');
            scope.$watch(
                'uc.isUpdating',
                (newValue) => (newValue) ? liElement.removeClass('hidden') : liElement.addClass('hidden')
            );
        }
    }
}

class UpdatingController {
    constructor(ngstomp, $scope) {
        this.ngstomp = ngstomp;
        this.isUpdating = false;
        this.$scope = $scope;

        this.ngstomp
            .subscribe('/app/updating', (message) => this.updateStatus(message), $scope)
            .subscribe('/topic/updating', (message) => this.updateStatus(message), $scope);
    }

    updateStatus(message) {
        this.isUpdating = JSON.parse(message.body);
    }
}

angular.module('updating', ['AngularStompDK'])
    .directive('updateStatus', () => new UpdatingDirective())
    .controller('UpdatingController', UpdatingController);

